import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Home', component: () => import('@/views/Home.vue'),meta:{title: '首页'} },
  { path: '/about', name: 'About', component: () => import('@/views/About.vue'),meta:{title: '编辑'} },
  { path: '/workers', name: 'Workers', component: () => import('@/views/Workers.vue'),meta:{title: '工作'} },
  { path: '/download', name: 'DownLoad', component: () => import('@/views/DownLoad.vue'),meta:{title: '下载'} },
  { path: '/blog', name: 'Blog', component: () => import('@/views/Blog.vue'),meta:{title: '博客'} },
  { path: '/blogdetail', name: 'BlogDetail', component: () => import('@/views/BlogDetail.vue'),meta:{title: '详情'} }
]

const router = new VueRouter({
  routes
})

export default router
